<template>
    <div class="amount">
        <div class="title">{{title}}</div>
        <div :class="`amount-block ${classByType}`">
            <div class="highlight-block" v-if="showHighlight"></div>
            <img class="amount-image" :src="amountImage">
            <div class="amount">{{formattedAmount}} {{unit}}</div>
        </div>
        <div class="absolute-block absolute" :class="classByType">
            <div class="absolute-left-block" v-if="(type==='balance') && closestPayoutTimestamp">
                <div class="next-payment">{{$localizeFilter('next_payment_date_2', [`${$dateTimeFilter(closestPayoutTimestamp, 'custom', 'dd.mm.yyyy')}`])}}</div>
            </div>
            <button class="underline-button absolute-button" v-if="type=='staked'" @click="$emit('showDeposits')">
                {{$localizeFilter('all_transactions')}}
            </button>

        </div>

        <!-- <audio>
            <source src="@/assets/audio/income_sound.mp3" type="audio/mpeg">
        </audio> -->
    </div>
</template>

<script>
import { stringLiteral } from '@babel/types';
import { defineComponent } from '@vue/composition-api'
import anime from 'animejs/lib/anime.es.js';
import utils from '@/utils/utils'
import localizeFilter from '../filters/localize.filter';

const sound  = require("@/assets/audio/income_sound.mp3");

export default defineComponent({
    props: {
        classOverride: {
            type: String,
            default: null
        },
        amount: {
            type: Number,
            required: true
        },
        // availableProfitToWithdraw: {
        //     type: Number,
        //     default: 0
        // },
        title: {
            type: String,
            required: true
        },
        type: {
            type: String, // staked, balance, income
            default: null
        },
        soundEnabled: {
            type:Boolean,
            default:false
        },
        closestPayoutTimestamp: {
            type: Number,
            default: null
        },
    },
    watch: { 
        amount(newVal, oldVal) {
            // console.log('updated amount, oldVal =', oldVal, ', newVal =', newVal)
            if ((newVal !== oldVal)) {
                this.$nextTick(()=>{
                    // if (this?.type === 'income') {

                        if (this.index>0 && newVal > oldVal) {
                            this.count = oldVal || 0
                            this.setCount(newVal)
                        } else {
                            this.index=1
                            this.count = newVal
                        }
                    // } else if (this.type === 'arbitrationTotalIncome') {
                    //     if (this.index>0 && newVal > oldVal) {
                    //         this.count = oldVal
                    //         this.setCount(newVal)
                    //         this.animateHighlight()
                    //         // play sound
                    //         if (this.soundEnabled) {
                    //             this.play()
                    //         } 
                    //     } else {
                    //         this.index=1
                    //         this.count = newVal
                    //     }
                    // } else {
                    //     this.count = newVal
                    // }
                })
            }
        }
    },
    setup() {
        const audio = new Audio(sound);
        audio.muted = true
        function play() {
            // console.log('play', audio)
            audio.muted = false
            audio.play()
        }
        return { play }
    },
    data: ()=> ({
        count: 0.0,
        index: 0,
        showHighlight: false
    }),
    methods: {
        animateHighlight() {
            if (this.showHighlight === false) {
                this.showHighlight = true
                setTimeout(()=>{
                    this.showHighlight = false
                }, 4500)
            }
        },
        setCount (val) {
            const obj = { n: this.count }
            anime({
                targets: obj,
                n: val,
                round: 1000000,
                duration: 10000,
                easing: 'linear',
                update: () => {
                this.count = obj.n
                }
            })
        },
    },
    computed: {
        formattedAmount() {
            return this.count.toFixed(6)
        },
        amountImage() {
            if (this.type == 'staked' || this.type == 'income') {
                return require('@/assets/newImages/coins/CapitalWhalePresaleToken.png')
            } else {
                return require('@/assets/newImages/coins/usdt-trc20.png')
            }
        },
        unit(){
            // return 'USDT'
            return (this.type === 'staked' || this.type === 'income')  ? 'CWPT' : 'USDT'
        },
        classByType() {
            if (this.type === 'balance') {
                return 'balance blue-background'
            } else {
                return `${this.type} green-background`
            }
        },
    },
    mounted() {
        // console.log('mounted, amount =', this.amount)
        // if (this.type !== 'income') {
            this.count = this.amount || 0
        // }
     },
})
</script>

