<template>
    <div class="staking" @click="hideDropdowns">
        <div class="title">{{$localizeFilter('staking_title')}}</div>
        <div class="subtitle">{{$localizeFilter('staking_subtitle')}}</div>
        
        <div class="amount-container">
            <div class="amount with-buttons">
                <AmountViewStaking :title="$localizeFilter('staked') + ':'" :amount="staked" :type="'staked'" @showDeposits="showDeposits"/>   
                <div class="buttons-flex">
                    <div class="buttons-flex-row">
                        <div class="wrapper">
                            <button class="basic-button button-first" @click.stop="showDepositOptions">{{$localizeFilter('staking_deposit_button')}}</button>
                            <DepositOptions class="deposit-options" v-if="showDepositDropdown" :currencyCode="'CWPT'" :from="'staking'" @selectOption='selectDepositOption' @close="showDepositDropdown=false"/>
                        </div>   
        
                        <div class="wrapper">
                            <button class="basic-button button-second" @click.stop="burnCWPTForFENIX()">{{$localizeFilter('staking_burn_button')}}</button>
                        </div>   
                        
                    </div>
                </div>   
            </div>

            <div class="staking-profitometer flex-item">
                <StakingProfitometer :profitability="stakngProfitability"/>
            </div>

            <div class="amount with-buttons">
                <AmountViewStaking :title="$localizeFilter('balance_usdt') + ':'" :amount="balance" :type="'balance'"  :closestPayoutTimestamp="closestPayoutTimestamp"/>   
                <div class="buttons-flex">
                    <div class="buttons-flex-row only-second-button">
        
                        <div class="wrapper">
                            <button class="basic-button button-second" @click.stop="showWithdrawOptions">{{$localizeFilter('staking_withdraw_button')}}</button>
                            <WithdrawOptions class="withdraw-options" v-if="showWithdrawDropdown" @selectOption='selectWithdrawOption' @close="showWithdrawDropdown=false"/>
                        </div>   
                        
                    </div>
                </div>   
            </div>

            <div class="amount flex-item">
                <AmountViewStaking class="amount-view" :title="$localizeFilter('amount_income_coin', ['CWPT']) + ':'" :amount="currentTotalIncome" :type="'income'"/>
            </div>


        </div>

        <div class="table-container">
            <StakingOperations ref="operations" class="operations" :type="'bonusAccount'" :coin="'USDT'" :showOptions="true"/>
        </div>

        <StakingBalanceWithdrawModal v-if="showBalanceWithdrawModal" :withdrawToWallet="withdrawToWallet" @closeModal="closeModals"/>
        <Modal v-if="showWithdrawToExternalWalletModal" :type="'stakingBalanceWithdraw'" @closeModal="closeModals" @showWithdrawalConfirmation="showWithdrawalConfirmation"/>
        <BonusWithdrawalConfirmationModal v-if="showWithdrawalConfirmationModal"  @closeModal="closeConfirmationModal" :withdrawId="withdrawId" :commission="commission" :coinTicker="'USDT'" />
    
        <StakingBalanceDepositModal v-if="showBalanceDepositModal" :source="depositSource" @closeModal="closeModals"/>
        <StakingBalanceDepositFromExternalWallet v-if="showDepositFromExternalWalletModal" @closeModal="closeModals"/> 

        <BurnCWPTForFENIXModal v-if="showBurnCWPTforFENIXModal" @closeModal="closeModals" :rateCWPTToFENIX="rateCWPTToFENIX" :rateCWPTToUSDT="rateCWPTToUSDT"  :staked="staked"/>
    </div>
    </template>
    
    <script>
    import { defineComponent } from '@vue/composition-api'
    import localizeFilter from '@/filters/localize.filter'
    import AmountViewStaking from '../components/AmountViewStaking.vue'
    import StakingOperations from '../components/StakingOperations.vue'
    import DepositOptions from '../components/DepositOptions.vue'
    import WithdrawOptions from '../components/WithdrawOptions.vue'
    import StakingBalanceDepositModal from '../components/newModals/StakingBalanceDepositModal.vue'
    import StakingBalanceDepositFromExternalWallet from '../components/newModals/StakingBalanceDepositFromExternalWallet.vue'
    import StakingBalanceWithdrawModal from '../components/newModals/StakingBalanceWithdrawModal.vue'
    import BonusWithdrawalConfirmationModal from "@/components/newModals/BonusWithdrawalConfirmationModal.vue"
    import Modal from '../components/modals/Modal.vue'
    import BurnCWPTForFENIXModal from '../components/newModals/BurnCWPTForFENIXModal.vue'
    import StakingProfitometer from '../components/StakingProfitometer.vue'
    import { ref } from '@vue/composition-api'
    
    // async function wait (ms = 0) {
    //         return new Promise((resolve, reject) => {
    //             setTimeout(() => {
    //                 resolve(null);
    //             }, ms);
    //         });
    // }
    
    let allowedExchange = new Set([
      2, 49018, 1529, 22934, 4
    ])
    
    export default defineComponent({
        props: {
            poolTypeOverride: {
                type: String,
                default: null
            }
        },
        components: {
            AmountViewStaking,
            StakingOperations,
            DepositOptions,
            WithdrawOptions,
            StakingBalanceWithdrawModal,
            BonusWithdrawalConfirmationModal,
            Modal,
            StakingBalanceDepositModal,
            StakingBalanceDepositFromExternalWallet,
            BurnCWPTForFENIXModal,
            StakingProfitometer
        },
        setup() {
        },
        watch: {
        },
        data: () => ({
            showDepositDropdown: false,
            showWithdrawDropdown: false,
            showBalanceDepositModal: false,
            depositSource: null, // wallet, bonusAccount, externalWallet
            showDepositFromExternalWalletModal: false,
 
            showBalanceWithdrawModal: false,
            withdrawToWallet: false,
            showWithdrawToExternalWalletModal: false,
            showWithdrawalConfirmationModal: false,

            showBurnCWPTforFENIXModal: false,
            interval: null,
            currentTotalIncome: null,
        }),
        computed: {
            stakingData() {
                return this.$store.getters.stakingData
            },
            balance() {
                // return Number(this.stakingData?.balance || 0)
                return Number(this.stakingData?.balance || 0) / 1000000
            },
            staked() {
                // return Number(this.stakingData?.staked || 0)
                return Number(this.stakingData?.staked /*.slice(0,-1) */ || '0') / 1e18
            },
            totalIncome() {
                // return Number(this.stakingData?.totalIncome || 0)
                return Number(this.stakingData?.totalIncome  /*? .slice(0,-1) */ || '0') / 1e18
            },
            closestPayoutTimestamp() {
                return this.stakingData?.closestPayoutTimestamp || null
            },
            rateCWPTToFENIX() {
                return  this.stakingData?.rateCWPTToFENIX || null
            },
            rateCWPTToUSDT() {
                return this.stakingData?.rateCWPTToUSDT || null
            },
            stakngProfitability() {
                return this.stakingData?.stakngProfitability
            },
            totalIncomeSpeed() {
                // return 100
                // return Number(this.stakingData?.totalIncomeSpeed || 0)
                return Number(this.stakingData?.totalIncomeSpeed /*?.slice(0,-1) */ || '0') / 1e18
            },
            totalIncomeTimestamp() {
                return this.stakingData?.totalIncomeTimestamp || 0
            },
        },
        methods: {
            burnCWPTForFENIX()  {
                this.showBurnCWPTforFENIXModal = true
            },
            showDeposits() {
                this.$router.push('/cabinet/stakingDeposits')
            },
            showDepositOptions() {
                this.showDepositDropdown = true
                this.showWithdrawDropdown = false
            },
            showWithdrawOptions() {
                this.showDepositDropdown = false
                this.showWithdrawDropdown = true
            },
            hideDropdowns() {
                this.showDepositDropdown = false
                this.showWithdrawDropdown = false
            }, 
            selectDepositOption(option) {
                if (option === 'from-wallet') {
                    this.depositSource = 'wallet'
                    this.showBalanceDepositModal = true
                } else if (option === 'from-bonus-account') {
                    this.depositSource = 'bonusAccount'
                    this.showBalanceDepositModal = true
                } else if (option === 'from-external-wallet') {
                    this.depositSource = 'externalWallet'
                    this.showDepositFromExternalWalletModal = true
                }
            },
            selectWithdrawOption(option) {
                if (option === 'to-wallet') {
                    this.withdrawToWallet = true
                    this.showBalanceWithdrawModal = true
                } else if (option === 'to-bonus-account') {
                    this.withdrawToWallet = false
                    this.showBalanceWithdrawModal = true
                } else if (option === 'to-external-wallet') {
                    this.withdrawToWallet = false
                    this.showWithdrawToExternalWalletModal = true
                }
            },
            closeModals() {
                this.showBalanceDepositModal = false
                this.depositSource = null
                this.showDepositFromExternalWalletModal = false
                this.showBalanceWithdrawModal = false
                this.withdrawToWallet = false
                this.showWithdrawToExternalWalletModal = false
                this.showBurnCWPTforFENIXModal = false
                this.reloadData()
            },
            showWithdrawalConfirmation(withdrawBalanceRequestId, fee) {
                this.closeModal()
                this.withdrawId = withdrawBalanceRequestId
                this.commission = fee || 0
                setTimeout(()=>{
                    this.showWithdrawalConfirmationModal = true
                })
            },
            // closeModal() {
            //     this.showWithdrawToExternalWalletModal = false
            //     // this.clearAll()
            // },
            closeConfirmationModal() {
                this.showWithdrawalConfirmationModal = false
                this.closeModal()
            },
            reloadData() {
                this.$refs.operations.reloadData()
                this.loadData(true)
            },
            async loadData(silentCatch) {
                const data = {currencyCode: 'CWPT'}
                try {
                    await this.$store.dispatch('getStakingData', data)
                }
                catch (e) {
                    if(!silentCatch) {
                        this.$error(e)
                    }
                } 
            },
      },
      async mounted() {
        await this.loadData()

        this.currentTotalIncome = this.totalIncome
        setTimeout(() => {
            this.currentTotalIncome = this.totalIncome + this.totalIncomeSpeed * ((+Date.now() + 10000 - this.totalIncomeTimestamp) / 1000 / 60 / 60 / 24 / 28)
        }, 0)

        this.interval = setInterval(() => {
            // console.log('interval', this.totalIncome, this.totalIncomeSpeed, this.totalIncomeSpeed * ((+Date.now() - this.totalIncomeTimestamp) / 1000 / 60 / 60 / 24 / 28))
            this.currentTotalIncome = this.totalIncome + this.totalIncomeSpeed * ((+Date.now() + 10000 - this.totalIncomeTimestamp) / 1000 / 60 / 60 / 24 / 28)
        }, 10000)
      },
      unmounted() {
        clearInterval(this.interval)
        this.interval = null
      }  
    })
    </script>
    
